/*
     mainNavChanged Subject  nav menu    
     main nav false main nav     true
     main nav hide  teacher-nav-header   
     navStatus MainNavModel  
     3    1) showMainNav hide or not
                          2) logout ,
                          3) teacherLogin 
           profileInfo  
    payment  prfileInfo() localstorage  userName  
    property 'name' not defined error   
      routing    welcomComponent    
    logout/isAuth/teacherLogin/mainNav    
          
*/

import { AuthService } from './auth/auth.service';
import { Component, OnInit, ChangeDetectorRef, AfterViewChecked, OnDestroy } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { UtilityService } from './Utility-shared/utility.service';
import { MainNavModel } from './Utility-shared/mainNavChange.model';
import { Subscription } from 'rxjs/Subscription';
import { ProfileInfo } from './auth/profile.model';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterViewChecked, OnDestroy {

  title = 'app';
  profileInfo: ProfileInfo;
  mainNavHide = false;
  isAuth = false;
  isteacherAuth = false;
  val = 0;

  utilitySubscription: Subscription;

  constructor(private utilityService: UtilityService,
              private authService: AuthService,
              private router: Router,
              private changeDetector: ChangeDetectorRef) {}

  ngOnInit() {

        this.utilitySubscription = this.utilityService.mainNavChanged.subscribe((navStatus: MainNavModel) => {
            this.mainNavHide = navStatus.showMainNav;

            console.log('main nav ', this.mainNavHide);

            if (navStatus.checkLogoutOrNot) {               // logout   

                                              this.isAuth = false;
                                              this.isteacherAuth = false;
            } else if (!navStatus.isTeacherLogin) {              //   
                                               this.isAuth = true;
                                               this.isteacherAuth = false;

            } else {                                        // login  click  teacher   
              this.isAuth = true;
              this.isteacherAuth = true;
            }
            this.profileInfo = this.authService.getProfileInfo();
            console.log(this.profileInfo);
          });

          this.router.routeReuseStrategy.shouldReuseRoute = function() {
                return false;
          };


          this.router.events.subscribe((evt) => {
              if (evt instanceof NavigationEnd) {
                  this.router.navigated = false;
                  window.scrollTo(0, 0);
              }
          });

  }

  ngOnDestroy() {
    this.utilitySubscription.unsubscribe();
  }
  ngAfterViewChecked() {
    this.changeDetector.detectChanges();
  }
}
